.firstTypeSlideBlock {
    display: block;
    background: #ee71ff;
    width: 100%;
    height: 100%;
    cursor: pointer;
    box-shadow: 3px 3px 12px #696969;
    transition: box-shadow 185ms;
}
.firstTypeSlideBlock img {
    height: 470px;
}
.secondTypeSlideBlock {
    display: block;
    background: #363636;
    margin-bottom: 25px;
    cursor: pointer;
    box-shadow: 3px 3px 12px #696969;
    transition: box-shadow 185ms;

}
.secondTypeSlideBlock img {
    width: 450px;
    height: 200px;
}

.secondTypeSlideBlock2 {
    display: block;
    background: #363636;
    cursor: pointer;
    box-shadow: 3px 3px 12px #696969;
    transition: box-shadow 185ms;

}
.firstTypeSlideBlock:hover, .secondTypeSlideBlock:hover, .secondTypeSlideBlock2:hover {
    box-shadow: 3px 3px 15px #3d3d3d;
}
.secondTypeSlideBlock2 img {
    width: 450px;
    height: 200px;
}

.panelItem {
    display: block;
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    color: #ffffff;
    height: 45px;
}

.priceItem {
    float: right;
    padding-right: 15px;
    background: #ffd32f;
    font-size: 18px;
    line-height: 45px;
    font-weight: 600;
}
.platformItem {
    line-height: 35px;
    padding: 0px 15px;
    font-size: 25px;
}
.panelSlideItems {
    padding-top: 50px;
}

@media (max-width: 991px) {
    .secondTypeSlideBlock {
        margin-top: 24px;
    }
}
@media (max-width: 1200px) {
    .firstTypeSlideBlock img {
        height: 320px;
    }
    .secondTypeSlideBlock img {
        height: 125px;
    }
    .secondTypeSlideBlock2 img {
        height: 125px;
    }
}
@media (max-width: 767px) {
    .firstTypeSlideBlock img {
        height: 250px;
    }
    .secondTypeSlideBlock img {
        height: 120px;
    }
    .secondTypeSlideBlock2 img {
        height: 120px;
    }
}