.wrapItems {
    padding: 0px;
    margin-top: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.wrapItems li {
    text-decoration: none;
    display: inline-block;
    margin: 0px 15px 15px 0px;
    color: white;
}

.panelItem {
    display: block;
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    color: #ffffff;
    height: 32px;
}

.priceItem {
    float: right;
    padding-right: 15px;
    background: #ffd32f;
    font-size: 15px;
    line-height: 32px;
    font-weight: 600;
}
.platformItem {
    line-height: 32px;
    padding-left: 15px;
    font-size: 25px;
}
.countSellImg {
    line-height: 25px;
    padding-left: 15px;
    font-size: 22px;
}
.countSell {
    line-height: 25px;
    padding-left: 5px;
    font-size: 15px;
    color: #ffffff;
    text-shadow: 0px 0px 14px rgba(145, 145, 145, 1);
    font-weight: 600;
}
.navLinkStyle {
    display: inline-block;
    box-shadow: 3px 3px 12px #696969;
    transition: box-shadow 185ms;
}
.navLinkStyle:hover {
    box-shadow: 8px 8px 15px #696969;
}
.itemLi a {
    text-decoration: none;
}
.paginator {
    display: flex;
    list-style: none;
    justify-content: center;
    font-size: 18px;
    color: #ff0000;
}
.paginator .itemLink{
    text-decoration: none;
    padding: 8px 15px;
    color: #181818;
    font-weight: 600;
    font-size: 16px;
}
.paginator .itemLink:hover{
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    border-radius: 100%;
}
.paginator .activeItem {
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    border-radius: 100%;
}
.imgItemStyle {
    width: 230px;
    height: 160px;
}
@media (max-width: 650px) {
    .wrapItems li img {
        width: 160px;
        height: 90px;
    }
}
@media(max-width: 1400px) {
    .imgItemStyle {
        width: 210px;
        height: 145px;
    }
}
@media(max-width: 1200px) {
    .imgItemStyle {
        width: 170px;
        height: 145px;
    }

}