.cartHeader {
    width: 30px;
    height: 30px;
    color: aliceblue;
}
.countCartHeader {
    margin-left: 10px;
    display: inline-block;
    width: 20px;
    height: 20px;
    background: #19c700;
    color: aliceblue;
    text-align: center;
    line-height: 20px;
    border-radius: 100%;
    font-weight: 600;
}
.wrapTwoNavbar {
background: #212529;
}
.itemsMobile {
    display: none;
}
.itemSupport {
    background: #19c700;
    padding: 5px 8px 7px 8px;
    color: aliceblue;
    cursor: pointer;
    font-weight: 600;
    font-size: 14px;
}
.itemPurchases {
    margin-left: 10px;
    background: #ff2929;
    padding: 5px 8px 7px 8px;
    color: aliceblue;
    cursor: pointer;
    font-weight: 600;
    font-size: 14px;
}

.linkHoverSolid {
    margin-right: 15px;
    display: inline-block;
    color: #131313;
    line-height: 2;
    text-decoration: none;
    cursor: pointer;
}
.linkHoverSolid:after {
    background-color: #ce0046;
    display: block;
    content: "";
    height: 3px;
    width: 0%;
    -webkit-transition: width .2s ease-in-out;
    -moz--transition: width .2s ease-in-out;
    transition: width .2s ease-in-out;
}
.linkHoverSolid:hover:after,
.linkHoverSolid:focus:after {
    width: 100%;
}

.linkHoverSolid2 {
    margin-right: 15px;
    display: inline-block;
    color: #efefef;
    line-height: 2;
    text-decoration: none;
    cursor: pointer;
}
.linkHoverSolid2 span {
    padding-left: 8px;
}
.linkHoverSolid2:hover {
    text-shadow: 0px 0px 1px #ffffff;
}
.paddingBurger {
    padding: 0px;
}
.catalogStyle {
    margin-right: 15px;
    display: inline-block;
    color: #ffd01f;
    line-height: 2;
    text-decoration: none;
    cursor: pointer;
}
.catalogStyle:hover {
    text-shadow: 0px 0px 1px #ffd232;
}
.catalogStyle span {
    padding-left: 8px;
}
.imgHeader2 {
    width: 16px;
    height: 16px;
}
@media (max-width: 1396px) {
    .catalogStyle {
        font-size: 13px;
        margin-right: 10px;
    }
    .linkHoverSolid2 {
        font-size: 13px;
        margin-right: 10px;
    }
    .imgHeader2 {
        width: 13px;
        height: 13px;
    }
}

@media (max-width: 991px) {
    .itemsDesktop {
        display: none;
    }
    .itemsMobile {
        display: flex;
    }
    .linkHoverSolid {
        padding: 0px 10px;
        font-size: 14px;
        font-weight: 400;
        line-height: 1;
    }
    .linkHoverSolid:after {
        background-color: #ce0046;
        display: block;
        content: "";
        height: 0px;
        width: 0%;
        -webkit-transition: none;
        -moz--transition: none;
        transition: none;
    }
    .linkHoverSolid:hover:after,
    .linkHoverSolid:focus:after {
        width: 0%;
    }

    .linkHoverSolid2 {
        padding: 0px 10px;
        font-size: 14px;
        font-weight: 400;
        line-height: 1;
    }

    .paddingBurger {
        padding: 0px 5px;
    }
}

@media (max-width: 400px) {
    .itemSupport {
        font-size: 12px;
    }
    .itemPurchases {
        font-size: 12px;
    }
    .searchStyle {
        width: 180px;
    }
}



