.menuItem {
    display: block;
    text-align: center;
    background: linear-gradient(0deg, rgba(252,223,24,1) 0%, rgba(247,245,51,1) 50%, rgba(255,217,40,1) 100%);
    height: 50px;
    line-height: 50px;
    font-size: 20px;
    color: #252525;
    font-weight: 400;
    font-family: inherit;
    box-shadow: 3px 3px 9px #737373;
    transition: box-shadow 185ms;
    cursor: pointer;
    border-radius: 3px;

}
.menuItem:hover {
    box-shadow: 3px 3px 12px #606060;
}
.menuItemActive {
    display: block;
    text-align: center;
    background: linear-gradient(0deg, rgba(29,29,29,1) 0%, rgba(51,51,51,1) 50%, rgba(20,20,20,1) 100%);
    height: 50px;
    line-height: 50px;
    font-size: 20px;
    color: #dadada;
    font-weight: 400;
    font-family: inherit;
    box-shadow: 3px 3px 9px #737373;
    transition: box-shadow 185ms;
    cursor: pointer;
    border-radius: 3px;
}
.menuItemActive:hover {
    box-shadow: 3px 3px 12px #606060;
}
.mainWrap {
    padding-top: 25px;
    background: radial-gradient(circle, rgba(247,247,247,1) 0%, rgba(203,203,203,1) 50%, rgba(238,238,238,1) 100%);
}
@media (max-width: 991px) {
    .menuItem {
        margin-bottom: 8px;
    }
    .menuItemActive {
        margin-bottom: 8px;
    }

}